/* class used for header */
.mainHead {
  background-color: rgb(248, 248, 248);
  color: #000;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 70px;
  z-index: 999999;
}

/* this class is used to center logo when path is for employer */
.centerHead {
  justify-content: center;
}

/* this class is used to center logo when path is for employer */
.centerHead{
    justify-content: center;
}

.mainHead .logoSide {
  font-size: 24px;
  font-weight: bold;
}

.mainHead ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.mainHead nav ul li {
  margin-right: 40px;
  display: flex;
}

.mainHead nav ul li a {
  margin: auto;
}

