.profilePage.userProfile {
  margin-top: 70px;
  padding: 0;
}

.profilePage.userProfile .banner_section {
  background-image: url(../../images/jobBanner.jpg);
  /*Image link:  https://unsplash.com/photos/person-typing-on-apple-cordless-keyboard-RYyr-k3Ysqg */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 400px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  color: white;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to bottom right, #727272, #4654c7);
    opacity: 0.8;
  }

  &:after {
    content: 'User Profile';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0.99;
    font-weight: bold;
  }
}

.profilePage.userProfile .container {
  margin-top: 100px;
}

.profilePage.userProfile .oneline_input:last-child {
  margin-bottom: 0px;
}

.profilePage.userProfile .update_button {
  margin-top: 0px;
  margin-bottom: 40px;
}

/* add profile page css */
.addProfileParent {
  margin-top: 70px;
  padding: 50px 0;
}

.addProfileParent .p_heading {
  margin-bottom: 30px;
  font-size: 34px;
}

.addProfileParent .input_div {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 30px;
}

.addProfileParent .input_div input,
textarea,
select {
  padding: 8px 10px;
  border: 1px solid #acacac;
  border-radius: 5px;
}

.addProfileParent .input_div .input_parent label {
  font-weight: bold;
}

.user_education h1,
.user_experience h1 {
  font-size: 22px;
  margin: 60px 0 20px 0;
}

.addProfileParent form {
  gap: 50px;
  width: 100%;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  padding: 40px;
  display: flex;
}

.addProfileParent form .form_groupArea {
  flex: 2;
}

.addProfileParent .submit_div {
  text-align: center;
  margin-top: 50px;
}

.addProfileParent .submit_div button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background: var(--primary-color);
  color: white;
}

.addProfileParent .submit_div button:hover {
  background: var(--primaryDark-color);
  font-size: 16px;
}

/* .addProfileParent .image_div {
    flex: 1;
} */

.addProfileParent .image_div img {
  width: 100%;
}

.addProfileParent .flex_pContainer {
  display: flex;
  justify-content: space-between;
  gap: 50px;
}

.addProfileParent .flex_pContainer input#imagePicker {
  width: 200px;
  height: 200px;
  background: #ebebeb;
  border-radius: 10px;
  position: relative;
  cursor: pointer;
}

