} */
.profilePicDiv {
  position: relative;
  width: 200px;
  height: 200px;
}

.profilePicDiv .top_div {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border: 1px solid rgb(209, 209, 209);
  cursor: pointer;
}

.profilePicDiv .top_div img {
  width: 100%;
  height: 100%;
}

.profilePage .personal_details .profile_input {
  margin-bottom: 0;
}

.profilePage.userProfile .update_button {
  margin-top: 40px;
}
.updatePassBtn {
  padding: 8px 12px;
  border: 0px;
  outline: 0px;
  background-color: rgb(40, 40, 40);
  color: white;
  border-radius: 4px;
  cursor: pointer;
  position: absolute;
  right: 50px;
  top: 5px;
}
.updatePassBtn:hover {
  background-color: rgb(0, 0, 0);
}

.updatePasswordParent {
  padding: 100px;
}
.updatePasswordParent form {
  max-width: 40%;
}

@media (max-width: 480px) {
  .img_prnt {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .profilePage .icon_content {
    flex: 1;
  }
  .user_detail {
    flex: 1;
  }
  .profilePage .info_area {
    margin-top: 30px;
    align-items: flex-start;
  }
  .profilePage.userProfile .container {
    padding: 0 20px;
  }
  .profilePage .info_area {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .profilePage .info_area .user_detail h2 {
    font-size: 16px;
    margin-bottom: 8px;
  }
  .profilePage .img_prnt .img_bg {
    margin-right: 0px;
  }
  .profilePage .personal_details {
    margin-top: 30px;
  }
  .personal_details .profile_input label {
    font-size: 16px;
  }
  .personal_details .profile_input textarea {
    margin-bottom: 30px;
  }
  .profilePage.userProfile .banner_section {
    min-height: 200px;
  }
  .profilePage.userProfile .container {
    margin-top: 40px;
  }
  .profilePage.userProfile .banner_section::after {
    font-size: 30px;
  }
}
